<style>
.my-cat-fish-outer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
@keyframes downdowndown {
  0%,
  100% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(-10deg);
  }
}
@keyframes upupup {
  0%,
  100% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
}
#bluefish {
  width: 20%;
}
#bluefish #fin-top,
#bluefish #fin-bottom {
  transform-origin: center;
  transform-box: fill-box;
  animation-duration: 3s;
  animation-timing-function: cubic-bezier(0.55, 0.06, 0.68, 0.19);
  animation-iteration-count: infinite;
}
#bluefish #fin-top {
  animation-name: downdowndown;
}
#bluefish #fin-bottom {
  animation-name: upupup;
}

#blue-cat-tail {
  transition: 2s;
}

@keyframes seaMove {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-500%);
  }
}
#svg-sea-background {
  animation: seaMove 30s cubic-bezier(0.4, 0, 1, 1) infinite;
}
@keyframes fishSwimming {
  20% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-50%) rotate(-10deg);
  }
  30% {
    transform: translateY(-50%) rotate(0deg);
  }
  36% {
    transform: translateY(0) rotate(30deg);
  }
  40% {
    transform: translateY(0) rotate(0deg);
  }
  45% {
    transform: translateY(-30%) rotate(-10deg);
  }
  55% {
    transform: translateY(-10%) rotate(30deg);
  }
  60% {
    transform: translateY(60%) rotate(30deg);
  }
  65% {
    transform: translateY(110%) rotate(-15deg);
  }
  70% {
    transform: translateY(30%) rotate(0deg);
  }
  75% {
    transform: translateY(10%) rotate(-15deg);
  }
  80% {
    transform: translateY(-20%) rotate(0deg);
  }
  90% {
    transform: translateY(-30%) rotate(0deg);
  }
  95% {
    transform: translateY(0%) rotate(0deg);
  }
  100%,
  0% {
    transform: translateY(0) rotate(0deg);
  }
}
#bluefish {
  transform-origin: center;
  transform-box: fill-box;
  animation: fishSwimming 30s cubic-bezier(0.4, 0, 1, 1) infinite;
}
</style>